import { Button, Card, Col, Container, Row } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import FeatherIcon from 'feather-icons-react';

// components
import { FormInput } from 'components/form';

const AddPost = () => {
    // form validation schema
    const schemaResolver = yupResolver(
        yup.object().shape({
            title: yup.string().required('Please enter a title'),
            description: yup.string().required('Please enter a description'),
            message: yup.string().required('Please enter content'),
        })
    );

    // form method
    const methods = useForm({ resolver: schemaResolver });
    const {
        handleSubmit,
        register,
        control,
        formState: { errors },
    } = methods;

    // handle form submission
    const onSubmit = () => { };

    return (
        <section className="section position-relative">
            <Container>
                <Row className="align-items-center">
                    
                        <Card className="shadow-none">
                            <Card.Body className="p-xl-0 p-0">
                                <h2 className="mb-2 mt-0 fw-medium">Create a new Article</h2>
                                <p className="mb-5">
                                    Please fill out the article details below
                                </p>

                                <form onSubmit={handleSubmit(onSubmit)}>
                                    <Row>
                                        <Col md={12}>
                                            <FormInput
                                                type="text"
                                                name="title"
                                                label="Post Title"
                                                placeholder="Give a Title"
                                                containerClass={'mb-3'}
                                                register={register}
                                                errors={errors}
                                                control={control}
                                            />
                                        </Col>
                                        <Col lg={12}>
                                            <FormInput
                                                type="text"
                                                name="description"
                                                label="Tagline"
                                                placeholder="A short description"
                                                containerClass={'mb-3'}
                                                register={register}
                                                errors={errors}
                                                control={control}
                                            />
                                        </Col>
                                        <Col lg={12}>
                                            <FormInput
                                                type="textarea"
                                                name="message"
                                                label="Body"
                                                placeholder="Type Your message..."
                                                rows={5}
                                                containerClass={'mb-3'}
                                                register={register}
                                                errors={errors}
                                                control={control}
                                            />
                                        </Col>
                                        <Row>
                                            <Col lg={{ offset: 0, span: 5 }}>
                                                <div>
                                                    <label>Upload Image</label>
                                                    <div>
                                                        <button>Choose Image</button>
                                                    </div>
                                                </div>
                                            </Col>
                                            <Col lg={{ offset: 0, span: 6 }}>
                                                <div>
                                                    <p>.</p><br/>
                                                </div>
                                            </Col>
                                        </Row>
                                        
                                        <Col lg="auto" className="mt-4 mb-0">
                                            <Button type="submit">
                                                Submit
                                                <span className="icon icon-xs text-white ms-1">
                                                    <FeatherIcon icon="send" />
                                                </span>
                                            </Button>
                                        </Col>
                                    </Row>
                                </form>
                            </Card.Body>
                        </Card>
                        {/* <Col lg={12}>    </Col> */}

                    
                </Row>
            </Container>
        </section>
    );
};

export default AddPost;
